body {
  background-color: #DDD;
  width: 100%;
  height: 100vh;
  padding: 0;
}

.App {
  text-align: center;
  padding: '0';
}

.picture {
  width: 314px;
  border: 3px solid gray;
  border-radius: 3px;
}

.walker {
  border: 1px solid #CCC
}

@media (max-width: 425px) {
  .picture {
    width: 314px;
    margin: auto;
    display: block;
  }

  .walker {
    width: 320px;
    text-align: center;
    margin: auto;
  }

  .bio {
    width: 300px;
    margin: auto;
    display: block;
  }
}

@media (min-width: 768px) {
  .picture {
    width: 320px;
    margin: 0;
    text-align: left;
    float: left;
  }

  .walker {
    width: 100vw;
  }

  .bio {
    width: 300px;
    display: inline-block;
    text-align: left;
    margin: 10px;
  }
}

@media (min-width: 1024px) {
  .picture {
    width: 320px;
    margin: 0;
    text-align: left;
    float: left;
  }

  .walker {
    width: 100vw;
  }

  .bio {
    width: 600px;
    display: inline-block;
    text-align: left;
    margin: 10px;
    margin-bottom: 20px;
  }
}



